.over {
    left: 45%;
}

.imggirl {
    width: 40vw;
    height: 30vw;

}

html {
    scroll-behavior: smooth;
    overflow-x: hidden;
}

.item {
    height: 40em;

    /* border-width: 3px;
    border-style: solid;
    border-image:
        linear-gradient(to bottom,
            rgb(255, 255, 255),
            rgba(0, 0, 0, 0)) 1 100%; */
}

.title {
    /* border-bottom: 3px solid rgba(0, 0, 0, 0.185); */
    box-shadow: 0 20px 50px rgba(0, 0, 0, .1);

}

.front {
    height: 50vh;
    background-image: url('detail-of-open-magazine-6348.jpg');
    background-position-y: 70%;
    margin-bottom: 20em;
}

.front h1 {
    font-size: 7em;
}

.nav {
    background-color: rgba(0, 0, 0, 0.274);
}

.headroom {
    will-change: transform;
    transition: transform 200ms linear;
}

.headroom--pinned {
    transform: translateY(0%);
}

.headroom--unpinned {
    transform: translateY(-100%);
}

::-webkit-scrollbar {
    width: 5px;
}


/* Handle */
::-webkit-scrollbar-thumb {
    background: rgba(0, 0, 0, 0.328);
    border-radius: 10px;
}

.swiper-container {
    width: 100%;
    height: 100%;
}

.swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
}